import React, { Component } from 'react';

import './style.css';

class App extends Component {
    render() {
        return (
            <div className='app'>
                <span className='app__img'>
                    <div className='wrapper' style={{backgroundImage: 'url(' + this.props.img + ')'}}></div>
                </span>
                <div className='app__content'>
                    <div className='wrapper'>
                        <h1 className='app__title'> </h1>
                        <p className='app__text'>
                                <b>"World of English"</b>.
                                ,    .  ,       !
                        </p>
                        <p className='app__text'>
                            <span><b> </b>:  ,  ,        .</span>
                            <span><b> </b>:   ,       ,     ,     .</span>
                        </p>
                        <p className='app__text'>
                                  ,       ,    ,      ,       .
                                   !
                        </p>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;


